@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400&display=swap);
.header {
  display: flex;
  align-items: center;
  /* position: fixed; */
  justify-content: space-around;
  width: 95%;
  height: 70px;
  padding: 10px 0;
  background-color: rgba(147, 112, 216, 0.7);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  border-radius: 10px;
  margin: 0 auto;
  color: var(--solid-bg);
}

.title {
  height: 100%;
  width: auto;
}

.search-container {
  position: relative;
  display: flex;
  align-items: center;
  width: 200px;
  height: 30px;
  justify-content: center;

  border-bottom: 2px solid var(--default-bg);
}

.input {
  border: none;
  background-color: transparent;
  padding: 0 10px;
  height: 100%;
  width: 100%;
  outline: none;
  color: var(--solid-bg);
  font-size: var(--font-large);
}

.icon-search {
  width: auto;
  height: 100%;
  fill: var(--solid-bg);
  padding: 0 4px;
  transition: background 300ms ease-in-out;
  border-radius: 100px;
}

.icon-search:hover {
  background-color: var(--icon-color);
}

.pokemon {
  background-color: #eee;
  border-radius: 20px;
  box-shadow: 0 3px 15px rgba(100, 100, 100, 0.5);
  margin: 10px;
  padding: 20px;
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.pokemon .img-container {
  background-color: white;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.pokemon .img-container img {
  margin-top: 20px;
  max-width: 90%;
}

.pokemon .info {
  margin-top: 20px;
}

.pokemon .number {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  font-size: 0.8em;
  padding: 5px 10px;
}

.pokemon .name {
  margin: 15px 0 7px;
  letter-spacing: 1px;
}

.container {
  max-width: 1240px;
  width: 100%;
  margin: 0 auto;
}

.pokemon_container {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

:root {
  --font-small: 12px;
  --font-medium: 14px;
  --font-large: 16px;
  --font-xlarge: 18px;
  --default-bg: #fff;
  --solid-bg: #f4f4f4;
  --main-color: #333;
  --text-color: #666;
  --light-color: #8e8e8e;
  --muted-color: #a5a5a5;
  --icon-color: #d8d8d8;
  --link-color: #42a5f5;
  --deafult-border: #ddd;
  --fire: #fddfdf;
  --grass: #defde0;
  --electric: #fcf7de;
  --water: #def3fd;
  --ground: #f4e7da;
  --rock: #d5d5d4;
  --fairy: #fceaff;
  --poison: #98d7a5;
  --bug: #f8d5a3;
  --dragon: #97b3e6;
  --psychic: #eaeda1;
  --flying: #f5f5f5;
  --fighting: #e6e0d4;
  --normal: #f5f5f5;
}

* {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  list-style: none;
  font-family: 'Lato';
}

body {
  background: #efefbb;
  background: linear-gradient(to right, #d4d3dd, #efefbb);
}

